import 'reflect-metadata';
import { DEFAULTS, Provider, isValidProvider, } from './core/config';
import { ErrorSeverity, debounce, } from './src/infrastructure/providers/ProviderUtils';
import { logger } from './src/infrastructure/providers/ProviderUtils';
import { fetchZaiModels } from './src/infrastructure/providers';
// ============================================================================
// Helper Functions
// ============================================================================
/**
 * Type guard to check if an element is a valid provider settings element
 */
function isProviderSettingsElement(element) {
    return element !== null && element.classList.contains('provider-settings');
}
/**
 * Type guard to check if an event target is a button with index data
 */
function isButtonWithIndex(target) {
    if (target === null || !(target instanceof HTMLButtonElement)) {
        return false;
    }
    return 'index' in target.dataset && typeof target.dataset.index === 'string';
}
/**
 * Validates tag key format
 * Pattern: lowercase letters, numbers, underscores only
 */
function isValidTagKey(key) {
    return /^[a-z0-9_]+$/.test(key);
}
/**
 * Validates hex color format
 */
function isValidHexColor(color) {
    return /^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/.test(color);
}
/**
 * Gets permission origin for a provider
 */
function getPermissionOrigin(provider, settings) {
    switch (provider) {
        case Provider.OLLAMA:
            if (settings.ollamaApiUrl) {
                return new URL(settings.ollamaApiUrl).origin + '/*';
            }
            throw new Error('Ollama API URL is required for permission request');
        case Provider.OPENAI:
            return 'https://api.openai.com/';
        case Provider.GEMINI:
            return 'https://generativelanguage.googleapis.com/';
        case Provider.CLAUDE:
            return 'https://api.anthropic.com/';
        case Provider.MISTRAL:
            return 'https://api.mistral.ai/';
        case Provider.DEEPSEEK:
            return 'https://api.deepseek.com/';
        case Provider.ZAI_PAAS:
            return 'https://api.z.ai/';
        case Provider.ZAI_CODING:
            return 'https://api.z.ai/';
        default:
            throw new Error(`Unknown provider: ${provider}`);
    }
}
// ============================================================================
// DOM Element Accessors
// ============================================================================
/**
 * Safely retrieves an element by ID with type assertion
 */
function getElementById(id) {
    return document.getElementById(id);
}
/**
 * Retrieves all general settings DOM elements
 */
function getGeneralSettingsElements() {
    const providerSelect = getElementById('provider');
    const generalForm = getElementById('provider-options-form');
    const generalStatusMessage = getElementById('provider-status-message');
    const statusMessage = getElementById('provider-status-message');
    const ollamaApiUrl = getElementById('ollama-api-url');
    const ollamaModel = getElementById('ollama-model');
    const openaiApiKey = getElementById('openai-api-key');
    const geminiApiKey = getElementById('gemini-api-key');
    const claudeApiKey = getElementById('claude-api-key');
    const mistralApiKey = getElementById('mistral-api-key');
    const deepseekApiKey = getElementById('deepseek-api-key');
    const zaiPaasApiKey = getElementById('zaiPaasApiKey');
    const zaiPaasModel = getElementById('zaiPaasModel');
    const zaiCodingApiKey = getElementById('zaiCodingApiKey');
    const zaiCodingModel = getElementById('zaiCodingModel');
    const minConfidenceThreshold = getElementById('min-confidence-threshold');
    const minConfidenceThresholdSlider = getElementById('min-confidence-threshold-slider');
    const confidenceValue = getElementById('confidence-value');
    if (!providerSelect || !generalForm || !generalStatusMessage || !statusMessage) {
        throw new Error('Required general settings elements not found');
    }
    return {
        providerSelect,
        generalForm,
        generalStatusMessage,
        statusMessage,
        ollamaApiUrl,
        ollamaModel,
        openaiApiKey,
        geminiApiKey,
        claudeApiKey,
        mistralApiKey,
        deepseekApiKey,
        zaiPaasApiKey,
        zaiPaasModel,
        zaiCodingApiKey,
        zaiCodingModel,
        minConfidenceThreshold,
        minConfidenceThresholdSlider,
        confidenceValue,
    };
}
/**
 * Retrieves all tag management DOM elements
 */
